import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import styles from './styles.module.css'
import { updateGraphStyle } from '../../actions/style'
import { setAutoPagination } from '../../actions/config'
import SingleQuestionView from './SingleQuestionView'

class PresentationView extends React.Component {
  static propTypes = {
    order: PropTypes.arrayOf(PropTypes.number).isRequired,
    content: PropTypes.object.isRequired,
    updateGraphStyle: PropTypes.func,
    setAutoPagination: PropTypes.func,
  }

  constructor(props) {
    super(props)
    this.state = {
      currentIndex: 0,
      showAnswer: false,
      scaleFactor: 1.2,
    }
  }

  componentDidMount() {
    this.props.setAutoPagination(false)
    this.setScaleFactor(this.state.scaleFactor)
  }
  close = () => {
    this.props.setScaleFactor(this.props.scaleFactor)
    this.props.setAutoPagination(true)
    this.props.onRequestClose()
  }
  next = () => {
    let currentIndex = this.state.currentIndex + 1
    if (currentIndex >= this.props.order.length) {
      currentIndex -= this.props.order.length
    }
    this.setState({ currentIndex, showAnswer: false })
  }
  previous = () => {
    let currentIndex = this.state.currentIndex - 1
    if (currentIndex < 0) {
      currentIndex += this.props.order.length
    }
    this.setState({ currentIndex, showAnswer: false })
  }
  showAnswer = () => this.setState({showAnswer: true})
  hideAnswer = () => this.setState({showAnswer: false})
  setScaleFactor(scaleFactor) {
    this.props.setScaleFactor(scaleFactor)
    this.setState({scaleFactor})
  }
  changeScaleFactor = evt => this.setScaleFactor(evt.target.value)

  renderAnswerButton() {
    if (this.state.showAnswer) {
      return <button onClick={this.hideAnswer}>Hide Answer</button>
    } else {
      return <button onClick={this.showAnswer}>Show Answer</button>
    }
  }
  
  render() {
    return (
      <div className={styles.PresentationView}>
        <div style={{marginBottom: '2mm'}}>
          <button onClick={this.close}>Back</button>
          <div style={{display: 'inline-block', width: '1cm'}}/>
          Scale:
          <input type="number" min=".4" max="3" step=".2" 
            value={this.state.scaleFactor} onChange={this.changeScaleFactor}/>
          <div style={{display: 'inline-block', width: '1cm'}}/>
            {this.renderAnswerButton()}
          <div style={{display: 'inline-block', width: '1cm'}}/>
          <button onClick={this.previous}>Previous</button>
          <button onClick={this.next}>Next</button>
        </div>
        {this.props.order.length ? (
          <SingleQuestionView 
            content={this.props.content[this.props.order[this.state.currentIndex]]}
            scaleFactor={this.state.scaleFactor}
            showAnswer={this.state.showAnswer}
            number={this.props.startNumbering + this.state.currentIndex}
          />
        ) : null}
      </div>
    )
  }
}

const mapStateToProps = state => ({
  order: state.document.order,
  content: state.document.content,
  startNumbering: state.document.startNumbering,
  graphScaleFactor: state.style.graph.scaleFactor,
})
const mapDispatchToProps = dispatch => ({
  setAutoPagination: bool => dispatch(setAutoPagination(bool)),
  setScaleFactor: scaleFactor => dispatch(updateGraphStyle({scaleFactor})),
})

export default connect(mapStateToProps, mapDispatchToProps)(PresentationView)