import React from 'react'
import PropTypes from 'prop-types'
import { renderJson } from '../../renderJson'
import styles from './styles.module.css'

class SingleQuestionView extends React.Component {
  static propTypes = {
    content: PropTypes.arrayOf(PropTypes.number).isRequired,
    showAnswer: PropTypes.bool.isRequired,
    scaleFactor: PropTypes.number.isRequired,
  }

  static defaultProps = {
    showAnswer: false,
    scaleFactor: 1,
    number: null
  }
  
  render() {
    const style = {
      '--vg-transform': `scale(${this.props.scaleFactor})`,
      '--vg-label-font-size': (1/this.props.scaleFactor) + 'em',
      '--doc-startnumber': this.props.number - 1,
      fontSize: this.props.scaleFactor + 'em',
    }
    const questionContent = this.props.content.slice(1)
    const renderType = this.props.number ? 'NumberedQuestion' : 'Question'
    return (
      <div className={styles.SingleQuestionView}>
        <div className="document" style={style}>
          {renderJson([renderType, ...questionContent])}
        </div>
        <div className={styles.AnswerSeparator}/>
        {this.props.showAnswer ? (
          <div className="document" style={style}>
            <span style={{fontSize: 'var(--ui-font-size)'}}>
              <h1 >Answer:</h1>
            </span>
            {renderJson(['AnswerKey', ...questionContent])}
          </div>
        ) : null}
      </div>
    )
  }
}

export default SingleQuestionView