import * as geometry from './geometry'
import * as picofermi from './picofermi'
import * as polynomials from './polynomials'
import * as transformations from './transformations'
import * as trigonometry from './trigonometry'
import * as othergenerators from './othergenerators'
import * as bonusquestions from './bonusquestions'
import * as simpleexamples from './simpleexamples'
import * as generatortests from './generatortests'

const modules = {
  geometry, 
  polynomials,
  othergenerators, 
  transformations,
  trigonometry,
  picofermi,
  //bonusquestions,
  //generatortests,
  //simpleexamples,
}

const sorting = {
  picofermi: {sort: 1, prefix: '* '},
  bonusquestions: {sort: 1, prefix: 'Bonus: '},
  generatortests: {sort: 2, prefix: 'Test: '},
  simpleexamples: {sort: 2, prefix: 'Example: '},
}

const result = {}
for (const k in modules) {
  const {sort=0, prefix=''} = sorting[k] || {}
  result[k] = {sort, prefix, generators: modules[k]}
}

export default result
