const roundTo = (n, d) => {
  const f = Math.pow(10, d)
  return Math.round(n*f) / f
} 

class Coordinate {
  constructor(x, y) {
    this.x = x
    this.y = y
  }

  static Polar(angle, radius=1, options={degrees: false}) {
    const x = Math.cos(options.degrees ? angle*Math.PI/180 : angle) * radius
    const y = Math.sin(options.degrees ? angle*Math.PI/180 : angle) * radius
    return new this(x, y)
  }

  static Cartesian(x, y) {
    return new this(x, y)
  }

  toObject({round}={}) {
    if (round !== undefined) {
      return {
        x: roundTo(this.x, round), 
        y: roundTo(this.y, round)
      }
    }
    return {x: this.x, y: this.y}
  }

  toArray(options) {
    const {x, y} = this.toObject(options)
    return [x, y]
  }

  toString(options) {
    const {x, y} = this.toObject(options)
    return `${x},${y}`
  }

  abs2() {
    return this.x * this.x + this.y * this.y
  }

  abs() {
    return Math.sqrt(this.abs2())
  }

  atan2({positive=false, degrees=true}={}) {
    let result = degrees 
      ? Math.atan2(this.y, this.x) * 180 / Math.PI 
      : Math.atan2(this.y, this.x)
    if (positive && result < 0) {
      const fullCircle = degrees ? 360 : 2 * Math.PI
      while (result < 0) {
        result += fullCircle
      }
    }
    return result
  }

  unit() {
    return this.constructor.Polar(this.atan2(), 1)
  }

  add(other) {
    return this.constructor.Cartesian(this.x + other.x, this.y + other.y)
  }

  subtract(other) {
    return this.constructor.Cartesian(this.x - other.x, this.y - other.y)
  }

  scale(a) {
    return this.constructor.Cartesian(this.x * a, this.y * a)
  }

  rotate(angle, {degrees=false, center=Point.Cartesian(0, 0)}) {
    if (center.x !== 0 || center.y !== 0) {
      return this.subtract(center).rotate(angle, {degrees}).add(center)
    }
    const radius = this.abs()
    const currentAngle = this.atan2()
    const rotateAngle = degrees ? angle * Math.PI / 180 : angle
    return this.constructor.Polar(currentAngle+rotateAngle, radius)
  }

  distance2(other) {
    return other.subtract(this).abs2()
  }
  
  distance(other) {
    return other.subtract(this).abs()
  }

  partition(other, ratio) {
    const x = this.x * (1-ratio) + other.x * ratio
    const y = this.y * (1-ratio) + other.y * ratio
    return this.constructor.Cartesian(x, y)
  }

  midpoint(other) {
    return this.partition(other, 0.5)
  }

  latex() {
    return '\\left(' + this.toString() + '\\right)'
  }
}

export class Point extends Coordinate {
  toVector() {
    return new Vector(this.x, this.y)
  }
  latex() {
    return '\\left(' + this.toString() + '\\right)'
  }
  svgPlot(options) {
    return ['Point', this.toObject(options)]
  }
}

export class Vector extends Coordinate {
  toPoint() {
    return new Point(this.x, this.y)
  }
  latex() {
    return '\\langle ' + this.toString() + '\\rangle '
  }
  svgPlot({initial={x: 0, y: 0}, ...options}) {
    const terminal = this.add(initial)
    return ['Path', {points: `${initial.x},${initial.y} ${terminal.x},${terminal.y}`, marks: '-->'}]
  }
}

export function bisectAngle(a, b, {degrees=false}={}) {
  const fullCircle = degrees ? 360 : 2*Math.PI
  let a2 = b
  while (a2 < a) {
    a2 += fullCircle
  }
  return (a + a2) / 2
}

export class Line {
  constructor(point, vector) {
    this.point = point
    this.vector = vector
  }

  static fromPoints(a, b) {
    const vector = b.subtract(a)
    return new this(a, vector)
  }

  intersection(other) {
    const matr = [
      [this.vector.x, -other.vector.x],
      [this.vector.y, -other.vector.y]
    ]
    const det = -this.vector.x * other.vector.y + this.vector.y * other.vector.x
    if (det !== 0) { // if det = 0, intersection is infinite or none
      const diffs = [other.point.x - this.point.x, other.point.y - this.point.y]
      const inv = [
        [-other.vector.y, other.vector.x],
        [-this.vector.y, this.vector.x]
      ]
      const a = (1.0/det) * (inv[0][0]*diffs[0] + inv[0][1]*diffs[1])
      return this.point.add(this.vector.scale(a))

      // const b = (1/det) * (inv[1][0]*diffs[0] + inv[1][1]*diffs[1])
      // return other.point.add(other.vector.scale(b))
    }
  }
}

export class Segment {
  constructor(a,b) {
    this.a = a
    this.b = b
  }

  toLine() {
    return Line.fromPoints(this.a, this.b)
  }

  length() {
    return this.b.subtract(this.a).abs()
  }

  svgPlot({markers='.-.', round}={}) {
    return ['Path', {points: `${this.a.toString({round})},${this.b.toString({round})}`, markers}]
  }
}

export class Polygon {
  constructor(...vertices) {
    this.vertices = vertices
  }

  getVertex(i) {
    if (i < 0) return this.getVertex(i + this.vertices.length)
    if (i > this.vertices.length-1) return this.getVertex(i - this.vertices.length)
    return this.vertices[i]
  }

  svgPlot({markers='.-.', round}={}) {
    return ['Polygon', {points: this.vertices.map(p => p.toString({round})).join(' '), markers}]
  }

  centroid() {
    let x = 0
    let y = 0
    for (const v of this.vertices) {
      x += v.x
      y += v.y
    }
    return new Point(x/this.vertices.length, y/this.vertices.length)
  }

  svgPlotLabels(labels, {distance="3mm", round=3}={}) {
    // this assumes vertices are in counter-clockwise order
    const result = []
    for (let i = 0; i < this.vertices.length; i++) {
      const prev = this.getVertex(i-1)
      const vertex = this.getVertex(i)
      const next = this.getVertex(i+1)
      const degrees = true
      result.push([
        'Overlay', {
          ...vertex.toObject({round}), 
          displacement: {
            angle: roundTo(
              bisectAngle(
                prev.subtract(vertex).atan2({degrees}), 
                next.subtract(vertex).atan2({degrees}), 
                {degrees}
              ), 2
            ),
            radius: distance
          }
        },
        labels[i]
      ])

    }
    return result
  }
}

export class Circle {
  constructor(center, radius) {
    this.center = center
    this.radius = radius
  }

  svgPlot({round}={}) {
    const {x, y} = this.center.toObject({round})
    const radius = round === undefined
      ? this.radius
      : roundTo(this.radius, round)
    return ['Circle', {cx: x, cy: y, r: radius}]
  }
}