import questionTypes from '../../questions'

const questionBank = {}

for (const module in questionTypes) {
  const {sort=0, prefix='', generators=questionTypes[module]} = questionTypes[module]
  for (const q in generators) {
    const generator = generators[q]
    const id = `${module}.${q}`
    const {name, tags, description} = generator.register()
    questionBank[id] = {sort, name: prefix+name, tags, description, generator}
  }
}

export default questionBank