import WebFont from 'webfontloader'
import { addFontFamily, setUITheme } from './actions/config'
import { setStyle } from './stylesetter'
import initializeDev from './initialize-dev'
import { includes } from 'lodash'

function loadFonts(store) {
  const toLoad = {
    custom: [
      'katex_ams',
      'katex_caligraphic',
      'katex_fraktur',
      'katex_main',
      'katex_math',
      'katex_sansserif',
      'katex_script',
      'katex_size1',
      'katex_size2',
      'katex_size3',
      'katex_size4',
      'katex_typewriter',
      'cmu_bright',
      'cmu_concrete',
      'cmu_sansserif',
      'cmu_serif',
    ],
    google: [
      'IBM Plex Serif', 
      'IBM Plex Sans', 
    ],
  }
  const toAdd = [
    'cmu_sansserif', 
    'cmu_serif', 
    'cmu_bright', 
    //'IBM Plex Serif',
    //'IBM Plex Sans',
  ]

  WebFont.load({
    custom: {
      families: toLoad.custom.map(f => `${f}:n4,i4,n7,i7`),
      urls: ['./stylesheets/styles.global.css']
    },
    //google: {families: toLoad.google.map(f => f + ':n4,i4,n7,i7')},
    fontactive: (family) => {
      console.log(`load font ${family} successful`)
      if (includes(toAdd, family)) {
        store.dispatch(addFontFamily(family))
      }
    },
    fontinactive: (family) => console.error(`load font ${family} failed`),
  })
}

function checkCookie(store) {
  const state = store.getState()
  let themeset = false
  const cookie = document.cookie
  if (cookie) {
    for (const c of cookie.split(';')) {
      const [k, v] = c.split('=')
      if (k === 'ui-theme') {
        store.dispatch(setUITheme(v))
        themeset = true
      }
    }
  }
  if (!themeset) {
    store.dispatch(setUITheme(state.config.uiTheme))
  }
}

export default function intialize(store) {
  loadFonts(store)
  checkCookie(store)
  setStyle(store.getState().style)
  //initializeDev(store)
  return store
}